  html * table > thead > tr > th,
html * table > tbody > tr > td,
html * table > tfoot > tr > td,
html .v-data-footer {
    font-size: 75% !important;
  }
}
@media only screen and (max-width: 61.999em) {
  html * table > thead > tr > th,
html * table > tbody > tr > td,
html * table > tfoot > tr > td,
html .v-data-footer {
    font-size: 56.25% !important;
  }
}
@media only screen and (max-width: 47.999em) {
  html * table > thead > tr > th,
