// Body
$body-bg: #f8fafc;
$body-bg-1: #e8f1fafb;
$body-bg-2: #e8f1fa;

// Typography
$font-family-sans-serif: "Open Sans", sans-serif;
$font-size-base: 1.2rem;
$line-height-base: 1.7;
$font-weight-base: 400;

// Colors
$primary: #99001c;
$primary-dark: #6b0c1db0;
$secondary: #262626;
$text: $secondary;
$white: #fff;
$line: #88868644;
$info: #2196f3;

// Dashboard
$main-content: 80%;
$mini-side-nav-width: 4rem;
$side-nav-width: 20%; //calc(20% - #{$mini-side-nav-width});
$max-width: 100%;
$row-gutter: 1rem;
$column-gutter: 1rem;
$column-gutter-1: 0.5rem;
$top-bar-z-index: 999999;

// Website
$nav-height: 10rem;
$nav-top-height: 4rem;
$container-padding: 15rem;
